.banner {
  width: 100%;
  height: 100vh;
  background-image: url("/src/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.banner__content {
  display: flex;
  align-items: center;
  height: 100vh;
  color: #fff;
}
.container {
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
}
.banner__text {
  width: 500px;
}
.banner__text h3 {
  font-size: 50px;
}
.banner__text h1 {
  font-size: 80px;
  line-height: 80px;
  font-weight: 900;
  margin-top: 20px;
}
.banner__text p {
  font-size: 25px;
  font-weight: 400;
  margin-top: 20px;
}
.btn-smart {
  background: rgb(255, 69, 79);
}
.banner-img {
  margin-top: 60px;
}

.imgbtn {
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px;
  padding: 20px 40px;
}
.btn-smart:hover {
  background-color: rgb(255, 0, 0);
}

/* Second Slide */

.slide2 {
  padding: 80px 0;
}

.slide2 h3 {
  color: rgba(37, 37, 37, 0.7);
  font-size: 30px;
  margin-bottom: 12px;
  font-weight: 700;
}
.slide2 h1 {
  font-size: 40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;
}
.slide2 p {
  font-size: 18px;
  color: #8b8b8b;
  font-weight: 300;
}

.slide2__btn {
  margin-top: 35px;
}
.slide2__img {
  width: 100%;
  overflow: hidden;
}

.slide2__img img {
  width: 100%;
}
.slide2 h1 {
  font-size: 25px;
}
.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.col-6 {
  width: 50%;
}
.p-25 {
  padding: 45px;
}

/*  */
